@import url(https://fonts.googleapis.com/css2?family=Rubik:wght@400;500;700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  height: 100%;
  width: 100vw;
  max-width: 1440px;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  position: relative;
}

@media (min-width: 700px){
  .App{
    height: 100vh;
  }
}
.app-name {
    margin: 1.5rem;
    padding:0;
    color: white;
    font-size: 1.4rem;
    font-family: 'Rubik', sans-serif;
    font-weight: 700;
}

header {
    position: relative;
    width: 100vw;
    min-height: 240px;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-align-items: center;
            align-items: center;
    background-image: url(/ip-address-tracker/static/media/pattern-bg.a60a3c85.png);
    background-repeat: no-repeat;
    background-size: cover;
}

@media (min-width: 700px){
    .app-name {
        font-size: 1.7rem;
    }
    header {
        min-height: 250px;
    }
}
.IP-search-bar-container {
    width: 27rem;
    max-width: 80%;
    display: -webkit-flex;
    display: flex;
    margin-bottom: 2rem;
    margin-left: 2rem;
    margin-right: 2rem;
}

input {
    border: none;
}

.IP-input{
    -webkit-flex-grow: 1;
            flex-grow: 1;
    border-radius: 10px 0px 0px 10px;
    font-size: 18px;
    font-family: 'Rubik', sans-serif;
    font-weight: 400;
    padding-left: 25px;
    color: hsl(0, 0%, 17%);
}

.IP-search{
    width: 50px;
    height: 50px;
    border-radius: 0px 10px 10px 0px;
    background-color: hsl(0, 0%, 0%);
    background-image: url(/ip-address-tracker/static/media/icon-arrow.2eaaa5ee.svg);
    background-repeat: no-repeat;
    background-position: center;
}

.IP-search:hover {
    background-color: hsl(0, 0%, 59%);
    cursor: pointer;
}

.IP_info_container {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    width: 80%;
    -webkit-justify-content: center;
            justify-content: center;
    background-color: white;
    border-radius: 0.8rem;
    box-sizing: border-box;
    padding: 1.5rem;
    position: absolute;
    top: 9.3rem;
    z-index: 401;
}

@media (min-width : 700px) {
    .IP_info_container {
        -webkit-flex-direction: row;
                flex-direction: row;
        top: 70%;
        width: 80%;
        padding: 2rem 1.5rem;
    }
}
.IP-info-element{
    -webkit-flex: 1 1;
            flex: 1 1;
    font-family: 'Rubik', sans-serif;
    padding: 0px 10px;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-align-items: center;
            align-items: center;
}

.IP-info-lable{
    font-size: 0.6rem;
    font-weight: 700;
    color: hsl(0, 0%, 59%);
    letter-spacing: 1px;
}

.IP-info-data {
    color: hsl(0, 0%, 17%);
    font-weight: 500;
    font-size: 1.2rem;
    padding: 5px 0px 10px 0px;
}

.IP-info-data:last-child{
    margin-bottom: 0px;
}

@media (min-width: 700px){
    .IP-info-element{
        -webkit-align-items: flex-start;
                align-items: flex-start;
    }

    .IP-info-element:not(:last-child){
        border-right: 1px solid hsl(0, 0%, 59%) ;
    }

    .IP-info-data{
        font-size: 1.5rem;
    }
}
.map {
    -webkit-flex-grow: 1;
            flex-grow: 1;
    height: 80vh;
    width: 100%;
}
